import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ScanPage } from '../scan/scan';
import { VoucherProvider } from '../../providers/voucher/voucher';
import { LoginPage } from '../login/login'
import { Storage } from '@ionic/storage';

/**
 * Generated class for the ProductsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-products',
  templateUrl: 'products.html',
})
export class ProductsPage {

  public products: any = [
    {
      'id': 1,
      'name': 'soap',
      'price': '3',
      'image': '../../assets/imgs/soap.jpg',
      'unit': 'USD'
    },
    {
      'id': 2,
      'name': 'toothbrush',
      'price': '2',
      'image': '../../assets/imgs/toothbrush.jpg',
      'unit': 'USD'
    },
    {
      'id': 3,
      'name': 'pear',
      'price': '1',
      'image': '../../assets/imgs/pear.jpg',
      'unit': 'USD'
    },
    {
      'id': 4,
      'name': 'rice',
      'price': '15',
      'image': '../../assets/imgs/rice.jpg',
      'unit': 'USD'
    },
    {
      'id': 5,
      'name': 'flour',
      'price': '10',
      'image': '../../assets/imgs/flour.jpg',
      'unit': 'USD'
    },
    {
      'id': 6,
      'name': 'toothpaste',
      'price': '5',
      'image': '../../assets/imgs/toothpaste.jpg',
      'unit': 'USD'
    },
    {
      'id': 7,
      'name': 'apple',
      'price': '1',
      'image': '../../assets/imgs/apples.jpeg',
      'unit': 'USD'
    },
    {
      'id': 8,
      'name': 'brush',
      'price': '9',
      'image': 'data:image/jpeg;base64,',
      'unit': 'USD'
    },
    {
      'id': 9,
      'name': 'cherry',
      'price': '6',
      'image': 'data:image/jpeg;base64,',
      'unit': 'USD'
    },
    {
      'id': 10,
      'name': 'book',
      'price': '11',
      'image': 'data:image/jpeg;base64,',
      'unit': 'USD'
    },
    {
      'id': 11,
      'name': 'cake',
      'price': '4',
      'image': 'data:image/jpeg;base64,',
      'unit': 'USD'
    }
  ];
  public isItemSelected: boolean = false;
  public myproduct: any;
  public selectedProduct: any = null;
  public allChosenProducts: Array<any> = [];
  public quantity: string = '0';
  public total: number = 0;
  public isClicked = 'white';
  public typing = 'quantity';
  public showHeaderActions = false;
  public errorMessage: string = '';
  public successMessage: string = '';

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public voucherProvider: VoucherProvider,
    private storage: Storage,
    ) {
  }

  goToScanPage(){
    this.voucherProvider.setPrice(this.total)
    let products = []
    this.allChosenProducts.forEach(product => {
      products.push({
        id: product.product.id,
        name: product.product.name,
        price: product.product.price,
        image: product.product.image,
        unit: product.product.unit,
        quantity: product.quantity
      })
    })
    this.voucherProvider.setProducts(products)
    this.navCtrl.push(ScanPage);
  }

  selectProduct(argProduct) {
    if (argProduct.id == this.selectedProduct) {
      this.isItemSelected = false;
      this.myproduct= null;
      this.selectedProduct = undefined;
      return null;
    } else {
      this.isItemSelected = true; 
      this.myproduct= argProduct;
      this.selectedProduct = argProduct.id;
    }
    this.quantity = '0';
  }

  clickOnNumber(number, id) {
    let toString = number.toString()
    if (this.typing === 'quantity') {
      if (this.quantity == '0') {
        this.quantity = toString;
      } else {
        this.quantity += toString;
      }
    } else if (this.typing = 'price') {
      if (this.myproduct.price == '0') {
        this.myproduct.price = toString;
      } else {
        this.myproduct.price += toString;
      }
    }
    
  }

  backspace() {
    if (this.typing === 'quantity') {
      let splitted = this.quantity.split('');
      splitted.pop();
      this.quantity.length == 1 ? this.quantity = '0' : this.quantity = splitted.join('');
    } else if (this.typing = 'price') {
      let splitted = this.myproduct.price.split('');
      splitted.pop();
      this.myproduct.price.length == 1 ? this.myproduct.price = '0' : this.myproduct.price = splitted.join('');
    }
  }

  addToBasket(product) {
    this.selectProduct(product);
    this.allChosenProducts.push({product: product, quantity: this.quantity, subTotal: product.price * parseInt(this.quantity)})
    this.quantity = '0';
    let intermediaryTotal = 0;
    this.allChosenProducts.forEach(element => intermediaryTotal += element.subTotal)
    this.total = intermediaryTotal;
  }

  clearItemList() {
    this.allChosenProducts = [];
    this.total = 0;
  }

  toggleHeaderActions() {
    this.showHeaderActions = !this.showHeaderActions
  }

  logout() {
    this.storage.get('vouchers').then(vouchers => {
      if (vouchers !== null && vouchers.length > 0 ) {
        this.errorMessage = 'You need to sync your data before loging out'
        setTimeout(() => { this.errorMessage = '' }, 3000);
      } else {
        this.storage.set('vendor', null)
        this.navCtrl.setRoot(LoginPage);
      }
    })
  }

  sync() {
    this.storage.get('vouchers').then(vouchers => {
      if (vouchers == null || vouchers.length === 0) {
        this.successMessage = 'Your are already up to date'
        setTimeout(() => { this.successMessage = '' }, 30000);
      } else {
        // Send the vouchers to the back
        this.storage.set('vouchers', [])
        this.successMessage = 'Data has been synced'
        setTimeout(() => { this.successMessage = '' }, 3000);
      }
    })
  }
}



